"use client"

import { useState, useEffect } from "react"
import * as tf from "@tensorflow/tfjs"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Thermometer, Droplets, Sun, Wind, Activity, BrainCircuit } from "lucide-react"
import { useToast } from "@/hooks/use-toast"

interface Room {
  id: string
  name: string
}

// Your scaler values (updated from latest Colab run)
const SCALER_MEAN = [
  21.44120578709908,
  25.38920115808988,
  195.76859309389798,
  721.1552879031536,
  0.00403457825736191
]

const SCALER_SCALE = [
  1.0294898306746725,
  2.4503972376406296,
  251.84274493163858,
  294.29231033446035,
  0.0006131763062187735
]

function scaleInput(input: number[]): number[] {
  return input.map((val, i) => (val - SCALER_MEAN[i]) / SCALER_SCALE[i])
}

export function RoomAnalyzerForm({ rooms }: { rooms: Room[] }) {
  const { toast } = useToast()

  const [model, setModel] = useState<tf.LayersModel | null>(null)
  const [isModelLoading, setIsModelLoading] = useState(true)

  const [selectedRoom, setSelectedRoom] = useState<string>("")
  const [temperature, setTemperature] = useState<string>("23.7")
  const [humidity, setHumidity] = useState<string>("26.2")
  const [light, setLight] = useState<string>("500")
  const [co2, setCo2] = useState<string>("750")
  const [humidityRatio, setHumidityRatio] = useState<string>("0.0047")

  const [prediction, setPrediction] = useState<number | null>(null)

  // Load model + debug info
  useEffect(() => {
    async function loadModel() {
      console.debug("Starting model load from /model/model.json")
      try {
        const loadedModel = await tf.loadLayersModel('/model/model.json')
        console.debug("Model loaded successfully!")
        console.debug("Model summary:", loadedModel.summary())
        console.debug("Input shape:", loadedModel.inputs[0].shape)
        setModel(loadedModel)
        setIsModelLoading(false)
      } catch (error: any) {
        console.error("Failed to load model:", error)
        toast({
          title: "Model Load Failed",
          description: error?.message || "Check console for details",
          variant: "destructive",
        })
        setIsModelLoading(false)
      }
    }
    loadModel()
  }, [toast])

  const handlePredict = async () => {
    if (!selectedRoom) {
      toast({ title: "Room Required", description: "Please select a room.", variant: "destructive" })
      return
    }
    if (!model) {
      toast({ title: "Model Not Ready", description: "Still loading…", variant: "destructive" })
      return
    }

    const rawInputs = [
      parseFloat(temperature) || 0,
      parseFloat(humidity) || 0,
      parseFloat(light) || 0,
      parseFloat(co2) || 0,
      parseFloat(humidityRatio) || 0,
    ]

    console.debug("Raw inputs:", rawInputs)

    const scaled = scaleInput(rawInputs)
    console.debug("Scaled inputs (z-score):", scaled.map(v => +v.toFixed(4)))

    try {
      const inputTensor = tf.tensor2d([scaled])
      console.debug("Input tensor shape:", inputTensor.shape) // should be [1, 5]

      const resultTensor = model.predict(inputTensor) as tf.Tensor
      console.debug("Raw model output tensor shape:", resultTensor.shape) // should be [1, 1]

      const probability = (await resultTensor.data())[0]
      console.debug("Prediction probability:", probability.toFixed(6))
      console.debug("Prediction class:", probability > 0.5 ? "Occupied" : "Vacant")

      setPrediction(probability)

      inputTensor.dispose()
      resultTensor.dispose()

      const isOccupied = probability > 0.5
      toast({
        title: isOccupied ? "Room Occupied" : "Room Empty",
        description: `Confidence: ${(probability * 100).toFixed(2)}%`,
        variant: isOccupied ? "default" : "secondary",
      })
    } catch (err: any) {
      console.error("Inference error:", err)
      toast({
        title: "Prediction Failed",
        description: err?.message || "Unknown inference error",
        variant: "destructive",
      })
    }
  }

  return (
    <div className="grid gap-6 lg:grid-cols-2">
      {/* LEFT CARD - FORM */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-xl">
            <BrainCircuit className="h-5 w-5" />
            AI Occupancy Predictor
          </CardTitle>
          <CardDescription>
            {isModelLoading ? "Loading Neural Network..." : "Model ready – Ready to predict"}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-5">

          {/* Room select */}
          <div className="space-y-2">
            <Label>Select Room</Label>
            <Select value={selectedRoom} onValueChange={setSelectedRoom}>
              <SelectTrigger className="rounded-2xl">
                <SelectValue placeholder="Choose a room" />
              </SelectTrigger>
              <SelectContent>
                {rooms.map((room) => (
                  <SelectItem key={room.id} value={room.id}>{room.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Inputs grid */}
          <div className="grid gap-4 sm:grid-cols-2">
            <div className="space-y-2">
              <Label className="flex items-center gap-1">
                <Thermometer className="h-4 w-4" /> Temp (°C)
              </Label>
              <Input type="number" value={temperature} onChange={(e) => setTemperature(e.target.value)} className="rounded-2xl" />
            </div>

            <div className="space-y-2">
              <Label className="flex items-center gap-1">
                <Droplets className="h-4 w-4" /> Humidity (%)
              </Label>
              <Input type="number" value={humidity} onChange={(e) => setHumidity(e.target.value)} className="rounded-2xl" />
            </div>

            <div className="space-y-2">
              <Label className="flex items-center gap-1">
                <Sun className="h-4 w-4" /> Light (Lux)
              </Label>
              <Input type="number" value={light} onChange={(e) => setLight(e.target.value)} className="rounded-2xl" />
            </div>

            <div className="space-y-2">
              <Label className="flex items-center gap-1">
                <Wind className="h-4 w-4" /> CO2 (ppm)
              </Label>
              <Input type="number" value={co2} onChange={(e) => setCo2(e.target.value)} className="rounded-2xl" />
            </div>

            <div className="space-y-2 col-span-2">
              <Label className="flex items-center gap-1">
                <Activity className="h-4 w-4" /> Humidity Ratio
              </Label>
              <Input type="number" step="0.0001" value={humidityRatio} onChange={(e) => setHumidityRatio(e.target.value)} className="rounded-2xl" />
              <p className="text-xs text-muted-foreground">e.g. 0.0047</p>
            </div>
          </div>

          <Button onClick={handlePredict} disabled={isModelLoading} className="w-full rounded-2xl py-6 text-lg font-medium">
            {isModelLoading ? "Loading Model..." : "Predict Occupancy"}
          </Button>
        </CardContent>
      </Card>

      {/* RIGHT CARD - RESULT */}
      <div className="space-y-6">
        {prediction !== null ? (
          <Card className={prediction > 0.5 ? "border-red-400 shadow-red-200" : "border-green-400 shadow-green-200"}>
            <CardContent className="flex flex-col items-center justify-center py-20 text-center">
              <h2 className="text-5xl font-bold mb-4">
                {prediction > 0.5 ? "Occupied" : "Vacant"}
              </h2>
              <p className="text-2xl text-muted-foreground">
                Confidence: {(prediction * 100).toFixed(2)}%
              </p>
              <p className="mt-4 text-sm text-muted-foreground">
                Raw probability: {prediction.toFixed(6)}
              </p>
            </CardContent>
          </Card>
        ) : (
          <Card className="border-dashed">
            <CardContent className="flex flex-col items-center justify-center py-20 text-center text-muted-foreground">
              Click predict to see results
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}